import React, { memo } from 'react';

// Libraries / Context
import { Link } from 'react-router-dom';

import { ReactComponent as Cancel } from './icons/cancel.svg';
import { ReactComponent as Pin } from './icons/pin.svg';
import { ReactComponent as Help } from './icons/help.svg';
import { ReactComponent as Settings } from './icons/settings.svg';
import { ReactComponent as Email } from './icons/email.svg';

const CancelBtn = ({theme}) => (
    <Link to="/" title="Click to close" className={"btn-nav" + theme}>
        <Cancel />
        <div>Close</div>
    </Link>
);

const PinBtn = ({theme, addPin, removePin, pinned, item}) => {
    if (pinned.includes(item)) {
        return (
            <div title="Click to pin" onClick={() => removePin(item)} className={"active-btn-nav" + theme}>
                <Pin />
                <div>Planned</div>
            </div>    
        );
    }

    return (
        <div title="Click to pin" onClick={() => addPin(item)} className={"btn-nav" + theme}>
            <Pin />
            <div>Plan</div>
        </div> 
    );
}

const EmailBtn = ({theme}) => (
    <div title="Click to email" className={"btn-nav" + theme}>
        <Email />
        <div>Email</div>
    </div>
);

const HelpBtn = ({theme}) => (
    <Link to="/" title="Get some helpful tips" className={"btn-nav" + theme}>
        <Help />
        <div>Help</div>
    </Link>
);

const SettingsBtn = ({theme}) => (
    <Link to="/" title="Go to settings" className={"btn-nav" + theme}>
        <Settings style={{margin: '8px 0 7px 0'}} />
        <div>More</div>
    </Link>
);

const Navigation = ({theme, type, addPin, removePin, pinned, item}) => {
    if (type === 'course') {
        return (
            <div className={'navigation' + theme}>
                <PinBtn 
                    theme={theme} 
                    addPin={addPin} 
                    removePin={removePin} 
                    pinned={pinned} 
                    item={item}
                />
                <CancelBtn theme={theme} />
            </div>
        );
    }

    else if (type === 'teacher') {
        return (
            <div className={'navigation' + theme}>
                <EmailBtn theme={theme} />
                <CancelBtn theme={theme} />
            </div>
        );
    }

    else if (type === 'home') {
        return (
            <div className={'navigation' + theme}>
                <HelpBtn theme={theme} />
                <SettingsBtn theme={theme} />
            </div>
        );
    }
};

export default memo(Navigation);